'use strict';

import {RedocComponent, BaseComponent} from '../base';
import JsonSchema from '../JsonSchema/json-schema';
import JsonSchemaLazy from '../JsonSchema/json-schema-lazy';

function safePush(obj, prop, item) {
  if (!obj[prop]) obj[prop] = [];
  obj[prop].push(item);
}

@RedocComponent({
  selector: 'params-list',
  template: `
    <h2 class="param-list-header" *ngIf="data.params.length"> Parameters </h2>
    <template ngFor [ngForOf]="data.params" #paramType="$implicit">
      <header class="paramType"> {{paramType.place}} Parameters </header>
      <div class="params-wrap">
        <div *ngFor="#param of paramType.params" class="param">
            <div class="param-name">
              <span class="param-name-content"> {{param.name}} </span>
            </div>
            <div class="param-info">
              <div>
                <span class="param-type {{param.type}}" [ngClass]="{'with-hint': param._displayTypeHint}"
                title="{{param._displayTypeHint}}"> {{param._displayType}} {{param._displayFormat}}</span>
                <span *ngIf="param.required" class="param-required">Required</span>
                <div *ngIf="param.enum" class="param-enum">
                  <span *ngFor="#enumItem of param.enum" class="enum-value {{enumItem.type}}"> {{enumItem.val | json}} </span>
                </div>
              </div>
              <div class="param-description" innerHtml="{{param.description | marked}}"></div>
            </div>
        </div>
      </div>
    </template>

    <div *ngIf="data.bodyParam">
      <h2 class="param-list-header" *ngIf="data.bodyParam"> Request Body </h2>

      <div class="body-param-description" innerHtml="{{data.bodyParam.description | marked}}"></div>
      <div>
        <json-schema-lazy [auto]="true" pointer="{{data.bodyParam.pointer}}/schema">
        </json-schema-lazy>
      </div>
    </div>
  `,
  styles: [`
    .param-list-header{border-bottom:1px solid #999;font-size:18px;padding:0.2em 0;margin:0.5em 0;color:#253137;font-weight:normal}.param-schema{padding-left:12.5px;border-left:1px solid #7D97CE}.param-wrap{position:relative}.param-schema:before{content:"";position:absolute;left:13.5px;top:20px;bottom:0;border-left:1px solid #7D97CE}.param-name{flex-grow:0;-ms-flex-grow:0;display:inline-block;font-size:14px;padding:10px 0 10px 0;font-weight:bold;box-sizing:border-box;line-height:20px;border-left:1px solid #7D97CE;white-space:nowrap;position:relative}.param-name-content{padding-right:25px;display:inline-block}.param-info{flex-grow:1;-ms-flex-grow:1;padding:10px 0;box-sizing:border-box;border-bottom:1px solid #ccc;display:inline-block}.param{display:flex;display:-ms-flexbox}.param-required{color:red;font-weight:bold;font-size:12px;line-height:20px;vertical-align:middle}.param-type{color:#999;font-size:12px;line-height:20px;vertical-align:middle;font-weight:bold}.param-type.array:before{content:"Array of ";color:#999}.param-type.string,.enum-value.string{color:rgba(0,80,0,0.7)}.param-type.integer,.param-type.number,.enum-value.number{color:rgba(74,139,179,0.8)}.param-type.object{color:rgba(0,50,159,0.7)}.param-type.boolean,.enum-value.boolean{color:firebrick}.param-type.with-hint{background-color:rgba(0,50,159,0.1);padding:0.2em 0;font-size:0.85em;border-radius:3px;cursor:help}.param-type.with-hint:before,.param-type.with-hint:after{content:"\\00a0"}.param-type-trivial{margin:10px 10px 0;display:inline-block}.param-name>span:before{content:"";display:inline-block;width:7px;height:7px;background-color:#7D97CE;margin:0 10px;vertical-align:middle}.param-name>span:after{content:"";position:absolute;border-top:1px solid #7D97CE;width:10px;left:0;top:20px}.param-wrap:first-of-type>.param>.param-name:before{content:"";display:block;position:absolute;left:-1px;top:0;border-left:2px solid #fff;height:20px}.param-wrap:last-of-type>.param>.param-name{position:static}.param-wrap:last-of-type>.param>.param-name:after{content:"";display:block;position:absolute;left:-1px;border-left:2px solid #fff;top:21px;background-color:white;bottom:0}.param-wrap:last-of-type>.param-schema{border-left-color:transparent}.param-schema .param-wrap:first-of-type .param-name:before{display:none !important}.param-enum{color:#666}.param-enum:before{content:"Values: {"}.param-enum:after{content:"}"}.param-enum>.enum-value:after{content:", "}.param-enum>.enum-value:last-of-type:after{content:none}header.paramType{margin:10px 0;text-transform:capitalize}.params-wrap{display:table;width:100%}.param-name{display:table-cell;vertical-align:top}.param-info{display:table-cell;width:100%}.param{display:table-row}.param:last-of-type>.param-name{border-left:0}.param:last-of-type>.param-name:after{content:"";display:block;position:absolute;left:0;border-left:1px solid #7D97CE;height:21px;background-color:white;top:0}.param:first-of-type .param-name:after{content:"";display:block;position:absolute;left:-1px;border-left:2px solid #fff;height:20px;background-color:white;top:0}
  `],
  directives: [JsonSchema, JsonSchemaLazy]
})
export default class ParamsList extends BaseComponent {
  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    let paramsList = this.schemaMgr.getMethodParams(this.pointer, true);

    paramsList = paramsList.map((paramData) => {
      let propPointer = paramData._pointer;
      return JsonSchema.injectPropData(paramData, paramData.name, propPointer);
    });

    let paramsMap = this.orderParams(paramsList);

    if (paramsMap.body && paramsMap.body.length) {
      let bodyParam = paramsMap.body[0];
      bodyParam.pointer = bodyParam._pointer;
      this.data.bodyParam = bodyParam;
      delete paramsMap.body;
    }

    this.data.noParams = !(Object.keys(paramsMap).length || this.data.bodyParam);

    let paramsPlaces = ['path', 'query', 'formData', 'header', 'body'];
    let params = [];
    paramsPlaces.forEach(place => {
      if (paramsMap[place] && paramsMap[place].length) {
        params.push({place: place, params: paramsMap[place]});
      }
    });
    this.data.params = params;
  }

  orderParams(params) {
    let res = {};
    params.forEach((param) => safePush(res, param.in, param));
    return res;
  }
}
